import {
  ChangeDetectorRef,
  Component,
  HostListener,
  OnInit,
} from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { NavigationEnd, Router, RouterModule } from '@angular/router';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { faArrowLeft, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import {
  LocalizeRouterModule,
  LocalizeRouterService,
} from '@gilsdav/ngx-translate-router';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { LazyLoadImageModule } from 'ng-lazyload-image';
import { DeviceDetectorService } from 'ngx-device-detector';
import { filter } from 'rxjs';
import { SCREEN_MD } from '../../../constants';

@Component({
  standalone: true,
  imports: [
    MatButtonModule,
    FontAwesomeModule,
    TranslateModule,
    LocalizeRouterModule,
    RouterModule,
    LazyLoadImageModule,
  ],
  selector: 'app-ready-for-change',
  templateUrl: './ready-for-change.component.html',
  styleUrls: ['./ready-for-change.component.scss'],
})
export class ReadyForChangeComponent implements OnInit {
  image = '/assets/images/shared/footer-image.jpg';
  locales = this.localizeRouterService.parser.locales;
  currentUrl = '';
  faArrow = faArrowRight;
  isMobileOrTablet = true;
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    if (event.target.innerWidth > SCREEN_MD) {
      this.isMobileOrTablet = false;
      this.image = '/assets/images/shared/footer-image.jpg';
    } else {
      this.isMobileOrTablet = true;
      this.image = '/assets/images/shared/footer-image-mobile.jpg';
    }
  }
  constructor(
    public localizeRouterService: LocalizeRouterService,
    private router: Router,
    private spy: ChangeDetectorRef,
    private deviceDetectorService: DeviceDetectorService
  ) {}

  ngOnInit(): void {
    this.detectDir();
    this.detectSize();
    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe(() => {
        this.detectDir();
      });
  }
  detectSize() {
    this.isMobileOrTablet =
      this.deviceDetectorService.isMobile() ||
      this.deviceDetectorService.isTablet();
    if (this.isMobileOrTablet) {
      this.image = '/assets/images/shared/footer-image-mobile.jpg';
    } else {
      this.image = '/assets/images/shared/footer-image.jpg';
    }
  }
  detectDir() {
    this.faArrow =
      this.localizeRouterService.parser.currentLang == 'ar'
        ? faArrowLeft
        : faArrowRight;
    this.spy.detectChanges();
  }
  navigate() {
    let translatedPath: any = this.localizeRouterService.translateRoute([
      '/contact-us',
    ]);
    this.router.navigate(translatedPath);
  }
}
