import { CommonModule } from '@angular/common';
import {
  Component,
  EventEmitter,
  HostListener,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import { RouterModule } from '@angular/router';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { faChevronDown } from '@fortawesome/free-solid-svg-icons';
import {
  LocalizeRouterModule,
  LocalizeRouterService,
} from '@gilsdav/ngx-translate-router';
import { TranslateModule } from '@ngx-translate/core';
import { MatIconModule } from '@angular/material/icon';
import { MatMenuModule } from '@angular/material/menu';
import { MatButtonModule } from '@angular/material/button';
import { ProductSubMenuComponent } from './product-sub-menu/product-sub-menu.component';
import { CallComponent } from '../../call/call.component';
import { DeviceDetectorService } from 'ngx-device-detector';
import { SCREEN_MD } from '../../../../constants';

@Component({
  standalone: true,
  selector: 'app-sub-header',
  templateUrl: './sub-header.component.html',
  styleUrls: ['./sub-header.component.scss'],
  imports: [
    MatMenuModule,
    MatButtonModule,
    LocalizeRouterModule,
    RouterModule,
    FontAwesomeModule,
    TranslateModule,
    CommonModule,
    MatIconModule,
    ProductSubMenuComponent,
    CallComponent,
  ],
})
export class SubHeaderComponent implements OnInit {
  @Output() public sidenavToggle = new EventEmitter();
  @Input() icon: string = '';

  locales = this.localizeRouterService.parser.locales;
  currentUrl = '';
  faArrow = faChevronDown;
  isMobileOrTablet = true;
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    if (event.target.innerWidth > SCREEN_MD) {
      this.isMobileOrTablet = false;
    } else {
      this.isMobileOrTablet = true;
    }
  }
  constructor(
    private deviceDetectorService: DeviceDetectorService,
    public localizeRouterService: LocalizeRouterService
  ) {}
  ngOnInit(): void {
    this.detectSize();
  }
  public onToggleSidenav = () => {
    this.sidenavToggle.emit();
  };
  onSidenavToggleSub() {
    this.sidenavToggle.emit();
  }
  detectSize() {
    this.isMobileOrTablet =
      this.deviceDetectorService.isMobile() ||
      this.deviceDetectorService.isTablet();
  }
}
