import { Component, HostListener, OnInit } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { SCREEN_MD } from '../../../constants';
import {
  LocalizeRouterModule,
  LocalizeRouterService,
} from '@gilsdav/ngx-translate-router';
import { RouterModule } from '@angular/router';
import { DeviceDetectorService } from 'ngx-device-detector';
import { LazyLoadImageModule } from 'ng-lazyload-image';

@Component({
  standalone: true,
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss'],
  imports: [TranslateModule, LocalizeRouterModule, RouterModule,LazyLoadImageModule],
})
export class FooterComponent implements OnInit {
  locales = this.localizeRouterService.parser.locales;
  currentUrl = '';
  isMobileOrTablet = true;
  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    if (event.target.innerWidth > SCREEN_MD) {
      this.isMobileOrTablet = false;
    } else {
      this.isMobileOrTablet = true;
    }
  }
  constructor(
    private deviceDetectorService: DeviceDetectorService,
    public localizeRouterService: LocalizeRouterService
  ) {}

  ngOnInit(): void {
    this.detectSize();
  }
  detectSize() {
    this.isMobileOrTablet =
      this.deviceDetectorService.isMobile() ||
      this.deviceDetectorService.isTablet();
  }
}
